
.App {
  margin-top:60px;
  align-items: center;
}

.welcome-box{
  border-style:groove;
  width:280px;
  height:550px;
  margin-left:600px;
}
.welcome-content{
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size:23px;
  font-weight: 900;
}
.welcome-para{
  font-weight:500;
  color: gray;
  font-size:14px;
}
.inside-content{
  margin-left: 10px;
  margin-top:380px;
  line-height: 4px;
}
.welcome-buttons{
  margin-left:5px;
}
.welcome-buttons button{
  border-radius:5px;
  padding:9px;
  width:250px;
  background-color:rgb(67, 67, 241);
  color:white;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: 100;
  border-style: none;
  margin-top:5px;
}
.welcome-buttons .already{
  background-color:rgb(220, 193, 245);
  color:black;
}
.create-account-container {
  width:400px;
  height:700px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-style: groove;
  margin-left:580px;
}

.form-box {
  width: 350px;
  padding: 15px;
  font-family: Arial, sans-serif;
}

.form-box h2 {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 20px;
}

label {
  display: block;
  font-size: 14px;
  margin-bottom: 5px;
  font-weight: 500;
  color: #333;
}

.required {
  color: #a100ff;
  margin-left: 2px;
}

input[type="text"],
input[type="email"],
input[type="password"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 6px;
  border: 1px solid #ddd;
  outline: none;
  font-size: 14px;
}

.radio-section {
  margin-bottom: 20px;
}

.radio-section p {
  margin-bottom: 10px;
  font-size: 14px;
}

.radio-section label {
  margin-right: 20px;
  font-size: 14px;
}

.submit-btn {
  width: 100%;
  padding: 12px;
  background: #a100ff;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.submit-btn:hover {
  opacity: 0.9;
}
button{
  cursor: pointer;
}
.login-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fafafa;
}

.login-box {
  width: 350px;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.login-box h2 {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 10px;
}

.subtitle {
  font-size: 14px;
  color: #777;
  margin-bottom: 20px;
}

label {
  display: block;
  font-size: 14px;
  margin-bottom: 5px;
  font-weight: 600;
  color: #a100ff; 
}

input[type="email"],
input[type="password"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 6px;
  border: 1px solid #ddd;
  outline: none;
  font-size: 14px;
}

.login-btn {
  width: 100%;
  padding: 12px;
  background: #c4c4c4;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  border: none;
  border-radius: 6px;
}
.account-container {
  margin-left: 600px;
  width:350px;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: #fafafa;
}

.account-box {
  width: 350px;
  padding: 20px;
  font-family: Arial, sans-serif;
  background: #fff;
  border: 1px solid #eaeaea;
}

.account-box h3 {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 15px;
}

.profile-card {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.profile-pic-wrapper {
  position: relative;
  width: 55px;
  height: 55px;
}

.profile-pic {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  object-fit: cover;
}

.camera-icon {
  position: absolute;
  bottom: 0;
  right: 0;
  background: #a100ff; /* purple circle */
  border-radius: 50%;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile-right {
  margin-left: 10px;
}

.profile-right h4 {
  margin: 0;
  font-size: 14px;
  font-weight: bold;
}

.email {
  font-size: 13px;
  color: #555;
}

.description {
  font-size: 13px;
  color: #555;
  line-height: 1.4;
  border-top: 1px dashed #ddd;
  padding-top: 10px;
}


